.menu-footer {
  a {
    color: inherit;
    padding: 3px 0;
    font-size: 16px;
    line-height: 1;
    transition: color 0.3s ease 0s;

    @include vp-1023 {
      font-size: 12px;
    }

    &:hover {
      text-decoration: underline;
      color: #dbd053;
    }
  }
}

.menu-footer__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 35px;
  list-style: none;
  max-width: 620px;
  padding: 0;
  margin: 0;
  margin-top: 7px;
  align-items: flex-start;

  @include vp-1023 {
    gap: 17px 25px;
    max-width: 420px;
    min-width: 470px;
  }

  @include vp-767 {
    flex-direction: column;
    min-width: 270px;
    gap: 8px 25px;
  }
}
