.contacts__container {
  display: grid;
  grid-template-columns: 48% 1fr;
  padding: 120px 0 121px;
  column-gap: 103px;

  @include vp-1023 {
    padding: 100px 0 102px;
    column-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    padding: 58px 0 66px;
    row-gap: 35px;
  }
}

.contacts__content {
  @include vp-767 {
    grid-row: 1 / 1;
  }
}
